/* 该文件用于创建整个应用的路由器 */

import VueRouter from 'vue-router'

// 引入组件
import About from "../pages/About.vue";
import Home from "../pages/Home.vue";
import Message from '../pages/Message.vue';
import News from '../pages/News.vue';
import Detail from '../pages/Detail.vue';

// 创建并暴露一个路由器
const router = new VueRouter({
    mode: 'history',
    // mode: 'hash',
    routes: [
        {
            name: 'guanyu',
            path: '/about',
            component: About,
            meta: {
                isAuth: true,
                title: '关于',
            },
        },
        {
            name: 'zhuye',
            path: '/home',
            component: Home,
            meta: {
                title: '主页',
            },
            children: [
                {
                    name: 'liuyan',
                    path: 'message',
                    component: Message,
                    meta: {
                        isAuth: true,
                        title: '留言',
                    },
                    children: [
                        {
                            name: 'xiaoxi',
                            path: 'detail',
                            component: Detail,
                            meta: {
                                isAuth: true,
                                title: '消息',
                            },

                            // props的第一种写法，值为对象，该对象中的所有key-value都会以props的形式传递
                            /* props: {
                                id: 1,
                                title: 'hello',
                            }, */

                            // props的第二种写法，值为布尔值，若布尔值为真，就会把该路由组件收到的所有params参数，以props的形式传给Detail组件。
                            // props: true,

                            // props的第三种写法，值为函数
                            props($route) {
                                return {
                                    id: $route.query.id,
                                    title: $route.query.title,
                                };
                            },
                            /* props({ query: { id, title } }) {
                                return { id, title };
                            }, */
                        },
                    ]
                },
                {
                    name: 'xinwen',
                    path: 'news',
                    component: News,
                    meta: {
                        isAuth: true,
                        title: '新闻',
                    },
                    // 独享路由守卫
                    beforeEnter: (to, from, next) => {
                        // 判断条件：是否需要鉴权
                        if (to.meta.isAuth) {
                            if (localStorage.getItem('school') === 'bilibili') {
                                next();
                            } else {
                                alert('暂无权限访问！');
                            }
                        } else {
                            next();
                        }
                    }
                },
            ]
        },
    ]
})

// 全局前置路由守卫，初始化时被调用，每次路由切换之前被调用
/* router.beforeEach((to, from, next) => {
    // console.log('@');
    console.log('前置路由守卫', 'to', to, 'from', from);
    // 判断条件：是否需要鉴权
    if (to.meta.isAuth) {
        if (localStorage.getItem('school') === 'bilibili') {
            next();
        } else {
            alert('暂无权限访问！');
        }
    } else {
        next();
    }
}) */

// 全局后置路由守卫，初始化时被调用，每次路由切换之后被调用
router.afterEach((to, from) => {
    console.log('后置路由守卫', 'to', to, 'from', from);
    document.title = to.meta.title || '路由守卫';
})

export default router