<template>
  <div>
    <ul>
      <li v-for="d in detailList" :key="d.id">
        <!-- 跳转路由并携带params参数，to的字符串写法 -->
        <!-- <router-link :to="`/home/message/detail/${d.id}/${d.title}`">
          {{ d.title }}
        </router-link>&nbsp;&nbsp; -->

        <!-- 跳转路由并携带params参数，to的对象写法 -->
        <router-link
          :to="{
            // path: '/home/message/detail',
            name: 'xiaoxi',
            // params参数，不能与path参数共用
            query: {
              id: d.id,
              title: d.title,
            },
          }"
        >
          {{ d.title }} </router-link
        >&nbsp;&nbsp;
        <button @click="pushShow(d)">push查看</button>
        <button @click="replaceShow(d)">replace查看</button>
      </li>
    </ul>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: "Message",
  data() {
    return {
      detailList: [
        { id: "001", title: "消息001" },
        { id: "002", title: "消息002" },
        { id: "003", title: "消息003" },
      ],
    };
  },
  methods: {
    pushShow(d) {
      // console.log(this.$router);
      this.$router.push({
        name: "xiaoxi",
        query: {
          id: d.id,
          title: d.title,
        },
      });
    },
    replaceShow(d) {
      this.$router.replace({
        name: "xiaoxi",
        query: {
          id: d.id,
          title: d.title,
        },
      });
    },
  },
  beforeDestroy() {
    console.log("Message组件即将被销毁");
  },
};
</script>

