<template>
  <h2>我是About的内容</h2>
</template>

<script>
export default {
  name: "About",
  /* beforeDestroy() {
    console.log("About组件即将被销毁");
  }, */
  mounted() {
    // console.log("About组件挂载完毕", this);
    console.log("about", this.$route);
  },

  /* 组件内路由守卫 */
  // 通过路由规则，进入该组件时被调用
  beforeRouteEnter(to, from, next) {
    console.log("About---beforeRouteEnter");
    // 判断条件：是否需要鉴权
    if (to.meta.isAuth) {
      if (localStorage.getItem("school") === "bilibili") {
        next();
      } else {
        alert("暂无权限访问！");
      }
    } else {
      next();
    }
  },

  // 通过路由规则，离开该组件时被调用
  beforeRouteLeave(to, from, next) {
    console.log("About---beforeRouteLeave");
    next();
  },
};
</script>

